      <el-button type="primary" v-on:click="setType(1)">报名上墙</el-button>
      <el-button type="primary" v-on:click="setType(2)">报名活动</el-button>
    </div>
    <div v-show="showForm">
      <p class="title">报名信息</p>
      <el-checkbox v-model="protocol" style="margin-bottom: 10px"
