        ><span class="red"> *</span></el-checkbox
      >
      <p class="sub-title">昵称:<span class="red"> *</span></p>

      <el-input v-model="nickName" class="def-input" type="text" placeholder="昵称" />
      <p class="sub-title">年龄:<span class="red"> *</span></p>
      <el-input v-model="age" class="def-input" placeholder="年龄" />
      <p class="sub-title">性别:<span class="red"> *</span></p>
      <el-select v-model="sexVal" placeholder="请选择性别">
