        </el-option>
      </el-select>
      <p class="sub-title">身高:<span class="red"> *</span></p>
      <el-input v-model="height" class="def-input" placeholder="cm" />
      <p class="sub-title" style="right: 85px">
        家乡及目前所在地:<span class="red"> *</span>
      </p>
      <el-input
        type="textarea"
        :rows="3"
        v-model="place"
        class="def-input"
        placeholder="请详细描述"
      />
      <p class="sub-title">学历:<span class="red"> *</span></p>
      <el-select v-model="edu" placeholder="请选择学历">
