    },
    beforeImgUpload(file) {
      const isJPG = file.type === "image/jpeg" || file.type === "image/png";
      const isLt3M = file.size / 1024 / 1024 < 3;

      if (!isJPG) {
        this.$message.error("上传图片只能是 JPG，PNG 格式!");
      }
      if (!isLt3M) {
        this.$message.error("图片大小不能超过 3MB! 请适当截取图片后重试");
      }
      return isJPG && isLt3M;
    },

