      this.showForm = true;
    },
    submit: function () {
      console.log(this.type);
      if (
        this.nickName == "" ||
        this.school == "" ||
        this.edu == "" ||
        this.age == "" ||
        this.sexVal == "" ||
        this.height == "" ||
        this.place == "" ||
        this.about == "" ||
        this.constellation == "" ||
        this.target == "" ||
        this.mail == "" ||
        this.qq == ""
      ) {
        this.$message("还有信息未填完");
        return;
      }
      if (this.protocol == false) {
        this.$message("请先同意用户协议");
        return;
      }
      if (this.nickName.length > 15) {
        this.$message("昵称过长!");
        return;
      }
      if (isNaN(Number(this.age))) {
        this.$message("输入的年龄不是数字!");
        return;
      }
      if (isNaN(Number(this.height))) {
        this.$message("输入的身高不是数字!");
        return;
      }
      // if (this.place.length < 10) {
      //   this.$message("请详细描述家乡及所在地");
      //   return;
      // }
      if (this.place.length > 50) {
        this.$message("请简略一些所在地描述");
        return;
      }
      if (this.about.length < 15) {
        this.$message("请用15个以上文字描述您");
        return;
      }
      if (this.target.length < 15) {
        this.$message("请用15个以上文字描述您的心动条件");
        return;
      }
      if (this.about.length > 1000) {
        this.$message("关于您的描述请简略一些");
        return;
      }
      if (this.target.length > 1000) {
        this.$message("您的心动条件请简略一些");
        return;
      }
      if (this.mail.length > 100) {
        this.$message("邮箱过长");
        return;
      }
      if (this.qq.length > 100) {
        this.$message("联系方式过长");
        return;
      }
      if (this.mail.indexOf("@") == -1) {
        this.$message("邮箱格式不正确");
        return;
      }
      if (this.type == 1) {
        this.axios
          .post("http://www.scgxtd.cn/public/user/upload/normal", {
            nickName: this.nickName,
            openid: this.openid,
            sex: this.sexVal,
            school: this.school,
            age: this.age,
            height: this.height,
            constellation: this.constellation,
            place: this.place,
            mail: this.mail,
            connect: this.qq,
            about: this.about,
            target: this.target,
            background: this.edu,
          })
          .then((res) => {
            console.log(res);
            if (res.data.code == 0) {
              this.$message("提交成功");
              this.showUp = true;
              this.showNormalUp = true;
              this.showSubmitBtn = false;
            } else if (res.data.code == -1) {
              this.$message("报名失败，msg:" + res.data.msg);
            }
          });
      } else if (this.type == 2) {
        this.axios
          .post("http://www.scgxtd.cn/public/user/upload/active", {
            nickName: this.nickName,
            openid: this.openid,
            sex: this.sexVal,
            school: this.school,
            age: this.age,
            height: this.height,
