            console.log(res);
            if (res.data.code == 0) {
              this.$message("提交成功");
              this.showUp = true;
              this.showActUp = true;
              this.showSubmitBtn = false;
            } else if (res.data.code == -1) {
              this.$message("报名失败，msg:" + res.data.msg);
            }
          });
      }
